import { Injectable } from '@angular/core';
import { Item } from '../../+builder/models/model';
import { Http } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import { BaseService } from '../../../shared/services/base.service';
declare var ga: Function;
@Injectable()
export class AnalyticService extends BaseService {
  private visitorKey: string;
  private visitorAnswers: any[] = [];


  constructor(private _http: Http) {
    super();
  }

  reInitVisitorAnswers() {
    this.visitorAnswers = [];
  }

  setVisitorAnswers(item: any) {
    this.visitorAnswers.push(item);
  }

  getVisitorAnswers(): any[] {
    return this.visitorAnswers;
  }

  setVisitorKey(key: string) {
    if (!this.visitorKey)
      ga('devteam.send', 'pageview', '/' + key);
    this.visitorKey = key;
  }

  getVisitorKey(): string {
    return (this.visitorKey == undefined || this.visitorKey == '') ? '' : this.visitorKey;
  }

  saveStats(appId: string, item: Item): Observable<any> {
    return this._http.post(
      this._url + '/analytic/save_stats',
      {
        appId: appId,
        item: item,
        key: this.getVisitorKey(),
        unsavedArray: this.getVisitorAnswers()
      },
      this.post_options()
    )
      .map(this.extractData)
      .catch(this.handleError);
  }

  saveLead(appId: string, lead: any): Observable<any> {
    return this._http.post(
      this._url + '/analytic/save_lead',
      {
        appId: appId,
        lead: lead,
        key: this.getVisitorKey()
      },
      this.post_options()
    )
      .map(this.extractData)
      .catch(this.handleError);
  }

  saveResult(appId: string, formula: any[]): Observable<any> {
    return this._http.post(
      this._url + '/analytic/save_result',
      {
        appId: appId,
        formula: formula,
        key: this.getVisitorKey(),
      },
      this.post_options()
    )
      .map(this.extractData)
      .catch(this.handleError);
  }

  saveOutCome(appId: string, formula: any): Observable<any> {
    return this._http.post(
      this._url + '/analytic/save_outcome',
      {
        appId: appId,
        formula: formula,
        key: this.getVisitorKey(),
      },
      this.post_options()
    )
      .map(this.extractData)
      .catch(this.handleError);
  }

  saveSectionResult(sectionId: string): Observable<any> {
    return this._http.post(
      this._url + '/analytic/save_section_result',
      {
        sectionId: sectionId,
        key: this.getVisitorKey(),
        unsavedArray: this.getVisitorAnswers()
      },
      this.post_options()
    )
      .map(this.extractData)
      .catch(this.handleError);
  }

  generateVisitorKey(appId: string, questions: Item[], utmObj: any): Observable<any> {
    return this._http.post(
      this._url + '/analytic/visitor_key',
      {
        appId: appId,
        key: this.getVisitorKey(),
        questions: questions,
        utm_param: utmObj
      },
      this.post_options()
    )
      .map(this.extractData)
      .catch(this.handleError);
  }

}



