import { Component, Input, Output, OnInit, EventEmitter, ViewEncapsulation } from '@angular/core';
import { JSONBuilder } from '../../../+builder/services/JSONBuilder.service';
import { environment } from '../../../../../environments/environment';
import { UrlShortner } from '../../../+builder/services/UrlShortner.service';
import { SubDomainService } from '../../../../shared/services/subdomain.service';
declare var window;
declare var FB;
@Component({
  selector: 'cta_shares',
  encapsulation: ViewEncapsulation.None,
  template: require('./cta-shares.template.html')
})

export class CtaShares implements OnInit {
  @Input() data: any;
  @Input() devMode: any;
  value: string;
  twitterSrcUrl: any;
  linkedInSrcUrl: any;
  resultLink: any;
  shortURL: any;
  title: any;
  description: any;

  constructor(private jsonBuilderHelper: JSONBuilder,
    private subDomainService: SubDomainService,
    private _urlShortner: UrlShortner) { }

  ngOnInit() {
    this.twitterInit();
  }

  twitterInit() {
    window.twttr = (function (d, s, id) {
      var js, fjs = d.getElementsByTagName(s)[0],
        t = window.twttr || {};
      if (d.getElementById(id)) return t;
      js = d.createElement(s);
      js.id = id;
      js.src = "https://platform.twitter.com/widgets.js";
      fjs.parentNode.insertBefore(js, fjs);
      t._e = [];
      t.ready = function (f) {
        t._e.push(f);
      };
      return t;
    } (document, "script", "twitter-wjs"));
  }

  updateResultLink() {
    this.description = this.jsonBuilderHelper.getJSONBuilt().cta.shareButtonName;
    this.title = this.jsonBuilderHelper.getJSONBuilt().cta.shareTitle;
    this.resultLink = environment.PROTOCOL + this.subDomainService.subDomain.sub_domain + '.' + environment.APP_EXTENSION + '/' + this.jsonBuilderHelper.getJSONBuilt().url;
    let fbImgUrl = environment.PROTOCOL + this.subDomainService.subDomain.sub_domain + '.' + environment.APP_EXTENSION + '/seo/' + this.jsonBuilderHelper.getJSONBuilt().url;
    this._urlShortner.googleShortner(fbImgUrl).subscribe(
      body => {
        this.shortURL = body.id;
      }
    );
    this.twitterSrcUrl = `https://twitter.com/intent/tweet?status=` + encodeURI(this.description) + `+` + this.resultLink;
    this.linkedInSrcUrl = `https://www.linkedin.com/shareArticle?mini=true&url=` + this.resultLink + `&title=` + encodeURI(this.title) + `
                &summary=`+ encodeURI(this.description) + `&source=LinkedIn`;
  }

  facebookShare() {
    this.updateResultLink();
    let image = 'http://process.filestackapi.com/A3ygIw4hISSCdApqW4SAwz/urlscreenshot=delay:3000/' + this.shortURL;
    FB.ui({
      method: 'feed',
      display: 'popup',
      name: this.title,
      //caption: this.title,
      description: this.description,
      link: this.resultLink,
      picture: image
    }, function (response: any) { });
  }

}



