import {Component, Input, OnInit, ViewEncapsulation} from '@angular/core';
import {environment} from '../../../../../environments/environment';

@Component({
  selector: 'poweredby',
  template: `
		<div class=" text-right" *ngIf="data.visible == true" >
			<div class=" powered-by">
				<span>Powered by </span>
				<a href="{{url}}" target="_blank">
						<img src="assets/images/builder/powered-by-logo.png" alt="Powered By">
				</a>
			</div>
		</div>
	`,
  encapsulation:ViewEncapsulation.None
})

export class PoweredByComponent implements OnInit {
  @Input() data: any;
  url: string;

  ngOnInit() {
  	this.url = environment.PROTOCOL+ environment.APP_EXTENSION;
  }
}



