import { Directive, ElementRef, Input, OnInit } from '@angular/core';
import { JSONBuilder } from '../../+builder/services/JSONBuilder.service';


declare var math: any;
declare var jQuery: any;
@Directive({
  selector: '[result]',
})

export class Result implements OnInit {

  @Input('result') result: any;
  private ele: HTMLElement;

  constructor(el: ElementRef, private jsonBuilderHelper: JSONBuilder) {
    this.ele = el.nativeElement;
  }


  ngOnInit() {
    let dataText = this.result.props.title;
    /* parse Q values /*/
    dataText = this.result.props.title.replace(/({Q[\d]+})/g, (match: any) => {
      let qIndex = Number(match.split(/[{Q}]/)[2]);
      return this.result.stats[qIndex - 1].value || match;
    });
    /* parse R values */
    dataText = this.result.props.title.replace(/({R[\d]+})/g, (match: any) => {
      let qIndex = Number(match.split(/[{R}]/)[2]);
      let resultObj = this.result.result[qIndex - 1];
      let resultValue = (resultObj.postfix == 'false') ? (resultObj.unit + '' + resultObj.value) : (resultObj.value + '' + resultObj.unit);
      return resultValue || match;

    });

    this.ele.innerHTML = dataText;
  }

}



