import {Component, Input, AfterViewInit, ViewEncapsulation} from '@angular/core';

declare var jQuery: any;
@Component({
  selector: 'result_redo',
  encapsulation: ViewEncapsulation.None,
  template: `
    <div class="redo-link">
		<ul>
		    <li><span>|</span></li>
			<li><a  id="refresh-button" (click)="redoFun()"><i class="material-icons">replay</i></a></li>	
        </ul>
	</div>
			
`
})
export class RedoComponent implements AfterViewInit {
  @Input() data: any;
  value: string;
  redoFun(){
    window.location.reload(true);
  }
  ngAfterViewInit() {}
}



