import { Component, Input, Output, OnInit, EventEmitter, ViewEncapsulation } from '@angular/core';
import { AnalyticService } from '../../services/analytic.service';
import { SubDomainService } from '../../../../shared/services/index';
import { JSONBuilder } from '../../../+builder/services/JSONBuilder.service';
import { UrlShortner } from '../../../+builder/services/UrlShortner.service';
import { environment } from '../../../../../environments/environment';

declare var jQuery: any;
declare var FB: any;
declare var window: any;
@Component({
  selector: 'share_links',
  encapsulation: ViewEncapsulation.None,
  template: `
	<div class="share-link">
		<ul>
			<li *ngIf="isVisible('facebook')"><a (click)="facebookShare()"><i class="fa fa-facebook-square" aria-hidden="true"></i></a></li>
			<li *ngIf="isVisible('twitter')"><a target="_blank" [href]="twitterSrcUrl" ><i class="fa fa-twitter-square" aria-hidden="true"></i></a></li>
			<li *ngIf="isVisible('linkedin')"><a target="_blank" [href]="linkedInSrcUrl" ><i class="fa fa-linkedin-square" aria-hidden="true"></i></a></li>
			<li *ngIf="isVisible('email')"><a target="_blank" [href]="mailSrcUrl" ><i class="fa fa-envelope" aria-hidden="true"></i></a></li>
		</ul>
	</div>
`,
  providers: [UrlShortner]
})
export class ShareLinks implements OnInit {
  @Input() data: any;
  @Input() devMode: any;
  value: string;
  twitterSrcUrl: any;
  linkedInSrcUrl: any;
  resultLink: any;
  shortURL: any;
  mailSrcUrl: any;
  title: any;
  constructor(private _analyticService: AnalyticService,
    private subDomainService: SubDomainService,
    private jsonBuilderHelper: JSONBuilder,
    private _urlShortner: UrlShortner
  ) { }
  redirectto(url: any) {
    var win = window.open(url, '_blank');
    win.focus();
  }
  ngOnInit() {

    this.title = this.jsonBuilderHelper.getJSONBuilt().title;

  /*  if (this.jsonBuilderHelper.getJSONBuilt().templateType == 'Numerical')
    this.title = 'I Got ' + this.jsonBuilderHelper.getJSONBuilt().formula[0].value + '.';
  else
      this.title = 'I Got ' + this.jsonBuilderHelper.getSelectedFormula().name + '.';
*/
    this.resultLink = environment.PROTOCOL + this.subDomainService.subDomain.sub_domain + '.' + environment.APP_EXTENSION + '/' + this.jsonBuilderHelper.getJSONBuilt().url;

    let fbImgUrl = environment.PROTOCOL + this.subDomainService.subDomain.sub_domain + '.' + environment.APP_EXTENSION + '/seo/' + this.jsonBuilderHelper.getJSONBuilt().url;
    this._urlShortner.googleShortner(fbImgUrl).subscribe(
      body => {
        this.shortURL = body.id;
      }
    );



    this.mailSrcUrl = 'mailto:?Subject=Calculator Result&Body=' + this.resultLink;
    this.twitterSrcUrl = `https://twitter.com/intent/tweet?status=` + encodeURI(this.title) + `+` + this.resultLink;
    this.linkedInSrcUrl = `https://www.linkedin.com/shareArticle?mini=true&url=` + this.resultLink + `&title=` + encodeURI(this.title) + `
                &summary=`+ encodeURI(this.jsonBuilderHelper.getJSONBuilt().description) + `&source=LinkedIn`;

    this.facebookInit();
  }

  facebookInit(){
    (function (d: any, s: any, id: any) {
      var js: any, fjs: any = d.getElementsByTagName(s)[0];
      if (d.getElementById(id)) { return; }
      js = d.createElement(s); js.id = id;
      js.src = '//connect.facebook.net/en_US/sdk.js';
      fjs.parentNode.insertBefore(js, fjs);
    } (document, 'script', 'facebook-jssdk'));

    window.fbAsyncInit = function () {
      FB.init({
        appId: environment.FB_API,
        xfbml: true,
        version: 'v2.8'
      });
    };
  }

  updateResultLink() {
    // if (!this.devMode) {
    // 	if (this._analyticService.getVisitorKey()) {
    // 		this.resultLink = 'http://' + this.subDomainService.subDomain.sub_domain +
    // 			'.' + Config.APP_EXTENSION + '/result/' +
    // 			this._analyticService.getVisitorKey();
    // 	}
    // }

    this.title =  this.jsonBuilderHelper.getJSONBuilt().title;

  /*  if (this.jsonBuilderHelper.getJSONBuilt().templateType == 'Numerical') {
      this.title = 'I Got ' + this.jsonBuilderHelper.getJSONBuilt().formula[0].value + '.';
    } else
      this.title = 'I Got ' + this.jsonBuilderHelper.getSelectedFormula().name + '.';*/

    this.resultLink = environment.PROTOCOL + this.subDomainService.subDomain.sub_domain + '.' + environment.APP_EXTENSION + '/' + this.jsonBuilderHelper.getJSONBuilt().url;

    let fbImgUrl = environment.PROTOCOL + this.subDomainService.subDomain.sub_domain + '.' + environment.APP_EXTENSION + '/seo/' + this.jsonBuilderHelper.getJSONBuilt().url;
    this._urlShortner.googleShortner(fbImgUrl).subscribe(
      body => {
        this.shortURL = body.id;
      }
    );
    this.mailSrcUrl = 'mailto:?Subject=Calculator Result&Body=' + this.resultLink;
    this.twitterSrcUrl = `https://twitter.com/intent/tweet?status=` + encodeURI(this.title) + `+` + this.resultLink;
    this.linkedInSrcUrl = `https://www.linkedin.com/shareArticle?mini=true&url=` + this.resultLink + `&title=` + encodeURI(this.title) + `
                &summary=`+ encodeURI(this.title) + `&source=LinkedIn`;
  }

  facebookShare() {
    this.updateResultLink();
    let image = 'http://process.filestackapi.com/A3ygIw4hISSCdApqW4SAwz/urlscreenshot=delay:3000/' + this.shortURL;
    FB.ui({
      method: 'feed',
      display: 'popup',
      name: this.title,
      //caption: this.title,
      description: this.jsonBuilderHelper.getJSONBuilt().description,
      link: this.resultLink,
      picture: image
    }, function (response: any) { });
  }

  isVisible(socialMedia: any) {
    for (let option in this.data.options) {
      if (this.data.options[option].type == socialMedia) {
        return this.data.options[option].selected;
      }
    }
    return true;
  }
}



