import { Directive, ElementRef, Input, OnInit, DoCheck } from '@angular/core';
import { JSONBuilder } from '../../+builder/services/JSONBuilder.service';
import { FormulaService } from '../../+builder/services/formula.service';

declare var math: any;
declare var jQuery: any;
@Directive({
  selector: '[fetch-result]',
  providers: [FormulaService]
})

export class FetchResult implements OnInit, DoCheck {

  @Input('fetch-result') formulaIndex: any;
  private ele: HTMLElement;

  constructor(el: ElementRef, private jsonBuilderHelper: JSONBuilder, private formulaService: FormulaService) {
    this.ele = el.nativeElement;
  }

  ngDoCheck() {
    this.ele.innerHTML = this.formulaService.formulaFunction(this.formulaIndex);
  }

  ngOnInit() {
    this.formulaService.formulaFunction(this.formulaIndex);
  }

}



