import { Directive, ElementRef, Input, OnInit, DoCheck} from '@angular/core';
import { JSONBuilder } from '../../+builder/services/JSONBuilder.service';

declare var jQuery: any;

@Directive({
  selector: '[themeColor]'
})

export class ThemeColor implements OnInit, DoCheck {

    @Input('themeColor') themeColor: any;
    private ele: HTMLElement;
    private colors: string[]=['#61bd6d','#1abc9c','#54acd2','#2c82c9','#9365b8','#475577','#cccccc',
    '#41a85f','#00a885','#3d8eb9','#2969b0', '#553982','#28324e','#000000','#f7da64','#fba026',
    '#eb6b56','#e25041','#a38f84','#efefef','#ffffff','#fac51c', '#f37934', '#d14841','#b8312f',
    '#7c706b', '#d1d5d8','#00aea5'];
    colorClass: string;

    constructor(el: ElementRef,private jsonBuilderHelper: JSONBuilder) {
         this.ele = el.nativeElement;
    }

    ngOnInit() {
        this.ngDoCheck();
    }

    ngDoCheck() {
        let color = this.jsonBuilderHelper.getJSONBuilt().themeColor;
        for(let atrribute of this.themeColor) {
            switch (atrribute) {
                case 'background':
                    this.ele.style.backgroundColor = color;
                    break;
                case 'border':
                    this.ele.style.borderColor = color;
                    break;
                case 'colorClass':
                    this.addColorClass(color);
                    break;
                default:
                    this.ele.style.color = color;
                    break;
            }
        }
    }

    addColorClass(color: string) {
        let colorIndex = (this.colors.indexOf(color)+1);
        let element =  jQuery(this.ele);
        let colorClass = 'tc'+colorIndex;
        if((colorIndex !== 0 && this.colorClass!=colorClass) || element.hasClass('w100')) {
            element
            .removeClass((index: any, css: any) => {
                return (css.match (/(^|\s)tc\S+/g) || []).join(' ')
            })
            .addClass(colorClass);
            this.colorClass = colorClass;
        }
    }
}



