import { Directive, ElementRef, Input, OnInit, DoCheck, OnChanges } from '@angular/core';
import { JSONBuilder } from '../../+builder/services/JSONBuilder.service';
import { FormulaService } from '../../+builder/services/formula.service';

declare var math: any;
declare var jQuery: any;
@Directive({
  selector: '[htmlProcess]',
  providers: [FormulaService]
})

export class HtmlProcessor implements OnInit, DoCheck {

  @Input('htmlProcess') html: any;
  private ele: HTMLElement;


  constructor(el: ElementRef, private jsonBuilderHelper: JSONBuilder, private formulaService: FormulaService) {
    this.ele = el.nativeElement;
  }

  ngDoCheck() {
    let tempHtml: any = this.formulaService.textParser(this.html);
    if (this.ele.innerHTML != tempHtml){
      this.ele.innerHTML = this.formulaService.textParser(this.html);
    }
  }

  ngOnInit() {
    this.ele.innerHTML = this.formulaService.textParser(this.html);
    //console.log("check",this.ele.innerHTML);
  }
}



