import {NgModule} from '@angular/core';
import {SharedModule} from '../../../shared/modules/shared.module';
import {DashboardComponent} from './dashboard.component';
import {MembershipService} from '../../../shared/services/membership.service';
import {CalculatorAnalytics} from '../+analytics/services/calculator-analytics.service';
import {BuilderService} from '../../+builder/services/builder.service';
import {DashboardService} from '../../../shared/services/dashboard.service';
import {Routes, RouterModule} from '@angular/router';
import {ControlsModule} from '../../templates/controls/controls.module';
import { ToolbarModule } from './../toolbar/toolbar.module';

const DASHBOARD_ROUTES: Routes = [
  {
    path: '',
    component: DashboardComponent
  }
]

@NgModule({
  imports: [SharedModule, RouterModule.forChild(DASHBOARD_ROUTES), ControlsModule, ToolbarModule],
  declarations: [DashboardComponent],
  providers: [DashboardService, BuilderService, CalculatorAnalytics, MembershipService],
  exports: [DashboardComponent]
})
export class DashboardModule {}



